.page-footer__logo {
    display: none;
}

.page-footer__socials {
    list-style: none;
    width: 320px;
    height: 80px;
    margin: 0;
    padding: 0;
    margin-top: 40px;
    margin-left: -30px;

    position: relative;
    display: flex;
    justify-content: center;

    background-color: #f1f1f1;
}

.page-footer__copyright {
    width: 320px;
    height: 80px;
    margin: 0;
    padding: 0;
    margin-left: -30px;

    display: flex;
    background-color: #f1f1f1;
}

.page-footer__copyright-text {
    font-size: 19px;
    line-height: 20px;
    font-weight: 700;
    color: #000000;

    display: inline-block;
    width: 130px;
    height: 26px;
    margin-left: 68px;
    margin-top: 26px;
}

.page-footer__copyright-link {
    display: inline-block;
    width: 27px;
    height: 34px;
    margin-top: 24px;
    margin-left: 26px;
}

@media (min-width: 768px) {
    .page-footer {
        display: flex;
        min-height: 140px;
        margin-top: 60px;   
        background-color: #f1f1f1;
    }

    .page-footer__logo {
        display: block;
        margin-top: 48px;
    }

    .page-footer__socials {
        width: 200px;
        margin: 0; 
        background-color: transparent;
        margin-top: 30px;
        margin-left: 130px; 
        margin-right: 55px;    

        &::after {
            display: none;
        }
    }

    .page-footer__copyright {
        width: 170px;
        margin: 0;
        margin-top: 32px;
        background-color: transparent;  
    }

    .page-footer__copyright-text {
        margin-left: 0;
    }
}

@media (min-width: 1050px) {
    .page-footer {  
        min-height: 143px;
        margin-top: 100px;  
    }  

    .page-footer__logo {  
        margin-top: 50px;
        margin-left: 51px;
    } 

    .page-footer__socials {
        margin-top: 32px;
        margin-left: 320px; 
        margin-right: 242px;
    }

    .page-footer__copyright {
        margin-top: 36px; 
    }
}


